
.foot {position: fixed;bottom: 0;width: 100%;height:7.34rem;transition: all .2s;
}
.leave {-webkit-transform: translate3d(0, 0, 0);transform: translate3d(0, 0, 0);
}
.enter {-webkit-transform: translate3d(0, 7.34rem, 0);transform: translate3d(0, 7.34rem, 0);
}



